@import url(https://fonts.googleapis.com/css?family=Roboto:300);
@import url(https://fonts.googleapis.com/css?family=Permanent+Marker);
@import url(https://fonts.googleapis.com/css?family=Roboto:300);
.btn {
    cursor: pointer;
    margin: 0;
    padding: 5px;
    text-align: center;
    text-decoration: none;
    transition: all 0.5s ease-out;
    width: 200px;
}

.btn:hover {
    color: white;
    transition: all 0.5s ease-out;
}

@media screen and (max-width: 400px) {
    .btn {
        font-size: 14px;
    }
}

.Header {
	background-image: linear-gradient(
		to right,
		transparent,
		silver,
		silver,
		transparent
	);
	display: grid;
	font-family: 'Roboto', sans-serif;
	opacity: 0.9;
	transition: all 0.5s ease-in-out;
}

.logo {
	align-items: center;
	display: flex;
	font-size: 1.8rem;
	justify-content: center;
}

.logo > h1 {
	margin: 0;
}

.bball-img {
	height: 55px;
	position: relative;
	top: 8px;
}

.header-nav {
	align-items: center;
	display: flex;
	font-size: 1.4rem;
	justify-content: space-evenly;
	margin: auto;
	width: 500px;
}

@media screen and (max-width: 760px) {
	.Header {
		background: darkgrey;
		grid-template-rows: 60px 30px;
	}

	.logo {
		font-size: 1.1rem;
	}

	.bball-img {
		height: 30px;
		position: relative;
		top: 3px;
	}

	.header-nav {
		font-size: 1rem;
	}
}

@media screen and (max-width: 500px) {
	.header-nav {
		width: 100vw;
	}

	.logo {
		font-size: 0.7rem;
	}

	.bball-img {
		height: 25px;
		position: relative;
		top: 3px;
	}
}

.PlayerInfo {
    align-items: center;
    background: rgb(147, 147, 147);
    color: white;
    display: flex;
    flex-direction: column;
    font-family: 'Roboto', sans-serif;
    justify-content: space-between;
    height: 70vh;
    margin: 2rem 0;
    min-height: 700px;
    opacity: 0.96;
    padding: 0 5%;
    position: relative;
    width: 50vw;
}

.player-info-name {
    font-size: 36px;
}

.player-info-pic {
    height: 200px;
    margin-bottom: 10px;
    opacity: 1.3;
}

.player-info-teams,
.player-info-totals {
    align-items: center;
    display: flex;
    height: 100px;
    justify-content: space-around;
    padding: 5px;
    width: 100%;
    font-size: 28px;
}

.player-info-totals {
    line-height: 1.5;
    min-height: 100px;
    text-align: center;
    width: 80%;
}

.player-info-teams {
    min-height: 60px;
}

.player-info-num {
    font-family: 'Permanent Marker', cursive;
    font-size: 50px;
    margin: 0;
}

.player-info-bonus {
    background: grey;
    margin-bottom: 30px;
    min-height: 200px;
    overflow: auto;
    text-align: center;
    width: 100%;
}

.player-info-bonus-title {
    font-family: 'Permanent Marker', cursive;
    font-size: 40px;
    margin: 0;
}

.eliminated {
    color: rgb(160, 0, 0);
    text-decoration: line-through;
}

.back-btn {
    background: rgb(84, 84, 84);
    border: none;
    color: white;
    cursor: pointer;
    font-size: 36px;
    min-height: 50px;
    padding: 10px 20px;
    position: absolute;
    right: 0px;
    transition: all 0.5s ease-out;
}

.back-btn:hover {
    background: rgb(227, 227, 227);
    color: rgb(84, 84, 84);
    transition: all 0.5s ease-out;
}

@media screen and (max-width: 1300px) {
    .player-info-teams,
    .player-info-totals {
        font-size: 22px;
    }
}

@media screen and (max-width: 1000px) {
    .PlayerInfo {
        height: auto;
        width: 70vw;
    }

    .player-info-teams {
        flex-direction: column;
        min-height: 140px;
    }

    .player-info-teams h5 {
        margin: 0;
    }

    .player-info-num {
        font-size: 40px;
    }
}

@media screen and (max-width: 500px) {
    .PlayerInfo {
        width: 80vw;
    }

    .player-info-teams,
    .player-info-totals {
        font-size: 18px;
    }

    .player-info-num {
        font-size: 30px;
    }

    .back-btn {
        font-size: 22px;
    }

    .player-info-bonus {
        font-size: 12px;
    }

    .player-info-bonus-title {
        font-size: 30px;
    }
}

.Player {
    align-items: center;
    background: rgb(147, 147, 147);
    color: white;
    cursor: pointer;
    display: flex;
    font-family: 'Roboto', sans-serif;
    font-size: 30px;
    height: 80px;
    margin: 10px 0;
    justify-content: space-between;
    padding-left: 20px;
    transition: all 0.5s ease-in-out;
}

.Player:hover {
    background: rgb(84, 84, 84);
    transition: all 0.5s ease-in-out;
}

.player-pic-wrapper {
    align-items: center;
    display: flex;
    height: 80px;
    justify-content: center;
    width: 80px;
}

.player-pic {
    max-height: 80px;
    max-width: 80px;
}

.player-name {
    width: 100px;
}

.player-rank {
    width: 60px;
}

@media screen and (max-width: 900px) {
    .Player {
        font-size: 22px;
    }
}

@media screen and (max-width: 760px) {
    .Player {
        font-size: 18px;
    }
}

@media screen and (max-width: 500px) {
    .Player {
        font-size: 16px;
        height: 60px;
    }
    .player-pic {
        height: 60px;
    }
}

.Standings {
    background: silver;
    height: 75vh;
    opacity: 0.96;
    overflow: auto;
    padding: 0 5%;
    width: 60vw;
}

.current-standings-header {
    align-items: center;
    background: rgb(84, 84, 84);
    color: white;
    display: flex;
    font-size: 22px;
    height: 50px;
    justify-content: space-between;
    margin-top: 10px;
    padding-left: 12px;
    padding-right: 32.5%;
}

.current-standings-points {
    position: relative;
    left: 20px;
}

@media screen and (max-width: 1300px) {
    .current-standings {
        width: 70%;
    }
}

@media screen and (max-width: 760px) {
    .current-standings {
        height: 80vh;
        width: 80%;
    }

    .current-standings-header {
        font-size: 16px;
        padding-right: 30%;
    }

    .current-standings-points {
        left: 10px;
    }
}

@media screen and (max-width: 500px) {
    .Standings {
        height: auto;
        margin: 20px 0;
        width: 80vw;
    }

    .current-standings-points {
        left: 30px;
    }
}

.Teams {
	opacity: 0.9;
	text-align: center;
}

.teams-wrapper {
	background: silver;
	display: flex;
	flex-wrap: wrap;
	margin: 10px auto;
	padding: 10px;
	width: 70vw;
}

.team {
	background: rgb(101, 101, 101);
	color: white;
	margin: 5px auto;
	width: 240px;
}

.red {
	background: rgb(108, 0, 0);
}

.green {
	background: rgb(0, 87, 0);
}

.team-controls {
	display: flex;
	justify-content: space-evenly;
	margin: 0 auto 2rem;
}

.teams-toggle-btn,
.teams-display-btn {
	background: rgb(84, 84, 84);
	border: 2px solid silver;
	color: white;
	cursor: pointer;
	font-size: 24px;
	margin-top: 30px;
	padding: 10px 30px;
	transition: all 0.5s ease-out;
	width: 220px;
}

.teams-toggle-btn:hover,
.teams-display-btn:hover {
	background: rgb(242, 242, 242);
	color: rgb(84, 84, 84);
	transition: all 0.5s ease-out;
}

.teams-search {
	height: 3rem;
	font-size: 24px;
	margin-top: 30px;
	width: 220px;
}

@media screen and (max-width: 960px) {
	.team-controls {
		flex-direction: column;
		align-items: center;
	}

	.teams-toggle-btn,
	.teams-display-btn,
	.teams-search {
		width: 300px;
	}
}

@media screen and (max-width: 760px) {
	.team-btn-wrapper {
		align-items: center;
		flex-direction: column;
		width: auto;
	}
}

@media screen and (max-width: 685px) {
	.team {
		width: 90%;
	}
}

@media screen and (max-width: 500px) {
	.teams-display-btn,
	.teams-toggle-btn,
	.teams-search {
		font-size: 16px;
		height: 42px;
	}
}

.Bonus {
    background: rgb(50, 50, 50);
    margin: 10px;
    padding: 5px;
    position: relative;
    text-align: left;
}

.BonusPage {
    background: silver;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    margin: 40px auto;
    opacity: 0.96;
    padding: 10px;
    width: 70vw;
}

.bonus-player {
    background: rgb(101, 101, 101);
    color: white;
    height: 320px;
    margin: 10px auto;
    overflow: auto;
    width: 240px;
}

.bonus-player-name {
    text-align: center;
    width: 240px;
}

@media screen and (max-width: 690px) {
    .BonusPage {
        padding: 0;
        width: 90vw;
    }

    .bonus-player,
    .bonus-player-name {
        width: 90%;
    }
}

.MainPage {
	align-items: center;
	display: flex;
	justify-content: center;
}

.error {
	background: rgba(255, 0, 0, 0.434);
	border-radius: 5%;
	border: 4px solid white;
    color: white;
	margin: 5rem auto;
	opacity: 0.95;
	padding: 5%;
	text-align: center;
	width: 50%;
}

@media screen and (max-width: 760px) {
    .error {
        width: 70%
    }
}

.loading-img {
	height: 150px;
	margin: auto;
}

.App {
	background: url(/long-shot-league/static/media/court.58e6fc11.jpg) center no-repeat;
	background-attachment: fixed;
	background-size: cover;
	display: grid;
	grid-template-rows: 120px 1fr;
	min-height: 100vh;
	transition: all 0.5s ease-in-out;
}

@media screen and (max-width: 760px) {
	.App {
		grid-template-rows: 90px 1fr;
	}
}

body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto',
        'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans',
        'Helvetica Neue', sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
        monospace;
}

